.player {
    height: 100px;
    color: whitesmoke;
    margin-bottom: 10px;
    margin-top: 20px;
    text-align: center;
    font-size: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.App {
  text-align: center;
}

#buttons {
  margin-left: 128px;
}

#buttons > button {
 margin-right: 12px;
 font-size: 23px;
}

.wheel > div:first-child {
  max-width: 700px;
  max-height: 700px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
